import {mapState} from 'vuex'
import player from '../../../components/player/player.vue'
export default{
	components:{
		player
	},
	data(){
		return{
			music :'',
			percent:0,
			isplay:false,
			timer:'',
			currentIndex:-1,
			currName:'',
			currentTime:'',
			duration:'',
			pindex:0,
			active:''
		}
	},
	onBackPress(options){
		if(options.from ==="navigateBack"){
			return false;
		}
		this.onreturn();
		return true
	},
	onLoad() {
		this.music = uni.createInnerAudioContext();
		this.music.onEnded( ()=>{
			console.log(111)
			this.percent =0;
			this.isplay = false;
			
			this.timer = undefined;	
			
			this.pindex++;
			console.log(this.pindex)
			console.log(this.musicLi.length)
			setTimeout(()=>{
				if(this.pindex <= this.musicLi.length - 1){
					this.isplay = true;
				}else{
					this.isplay = false;
				}
				
			},10)
			if(this.pindex > this.musicLi.length - 1){
				console.log(222)
				this.music.pause();
				this.isplay = false;
				this.active = ''
				return
			}
			
			console.log(this.pindex)
			this.music.src=this.musicLi[this.pindex].playUrl;
			this.music.play();
			this.currName =  this.musicLi[this.pindex].name;
			this.active = this.musicLi[this.pindex].id
			// this.currentIndex = this.currentIndex+1;
			// this.playorpause(index);
		})
	},
	 
	methods:{
		//子组件改变isplay
		changeIsPlay(val){
			console.log(val)
			this.isplay = val
			if(this.isplay){
				this.active = this.musicLi[this.pindex].id
				this.music.play();
			}else{
				this.active = ''
				this.music.pause();
			}
		},
		onreturn(){
			uni.navigateBack({
			})
		},
		playorpause(index,id){
			if(id === this.active){
				this.active = ''
				this.isplay = false
				this.music.pause();
				return
			}
			this.active = id
			
		
				 let str = JSON.stringify(this.$store.state.porps.playLists)
				 let arr = JSON.parse(str)
				 this.musicLi = arr.splice(index)
				 this.music.src=this.musicLi[this.pindex].playUrl
						
				//this.music.src = this.$store.state.porps.playLists[index].playUrl;
				console.log(this.music.src);
				this.currentIndex =this.musicLi[this.pindex];
				this.isplay = true;
				this.currName =  this.musicLi[this.pindex].name;
				this.music.play();
				this.getProcess();
			
		},
		getProcess(){
			this.timer = setInterval(()=>{
				this.currentTime = parseFloat(this.music.currentTime);
				this.duration = parseFloat(this.music.duration);
				this.percent = parseFloat(this.music.currentTime/this.music.duration).toFixed(4)
			},600)
		}
			
	},
	computed:{
		...mapState({
			count:state => state.porps,
			list:state => state.porps.playLists,
		}),
	},
	// filters:{
	// 	secondTime(time){
	// 		var minute = parseInt(time/60);
	// 		minute = minute<10?'0'+minute:minute;
	// 		var second = parseInt(time%60);
	// 		second = second<10?'0'+second:second;
	// 		return minute+':'+second;
	// 	}
	// }
	
	
}
